@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  /** background image */
  background-image: url(/Chess-React-Frontend/static/media/chesspicture2.8b169449.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-color: #e7e7e7;
}

main {
  margin-top: 7rem;
  text-align: center;
}


.button {
    display: inline-block;
    border-radius: 4px;
    background-color: #e4e4e4;
    border: none;
    color: #202020;
    text-align: center;
    font-size: 19px;
    font-weight: bold;
    padding: 20px;
    width: 200px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
    margin-bottom: 85px;
    font-family: Lucidatypewriter, monospace;

  }

  .button-location {
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
  }
  
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }

.main-title {
  color: #e4e4e4;
  text-align: center;
  font-size: 35px;
  height: 37px;
  margin-top: 120px;
  border-radius: 12px;
  font-family: Georgia, serif;
}

.sub-title {
  color: #e4e4e4;
  text-align: center;
  font-size: 11px;
  margin-bottom: 25px;
  border-radius: 12px;
}

.chess-icon {
  text-align: center;
  align-items: center;
}

.introduction {
