.new-settings {
    background-color: #e4e4e4;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .new-settings button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid rgb(6, 104, 231); 
    background-color: rgb(6, 104, 231); 
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
    margin-top: 10px;
  }
  
  .new-settings button:hover,
  .new-settings button:active {
    background-color: rgb(33, 120, 235); 
    border-color: rgb(33, 120, 235); 
  }

.new-settings__controls {
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: center;
  }
  
  .new-settings__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-settings__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
  
  .new-settings__actions {
    text-align: center;
  }

