    margin-left: 5px;
    
    
  }
.chess-board {
    display: grid;
    grid-template-columns: repeat(8, 75px);
    grid-template-rows: repeat(8, 75px);
    width: 600px;
    height: 600px;
}
.tile {
    width: 75px;
    height: 75px;
}

.black-tile {
    display: grid;
    place-content: center;
    background-color: rgb(6, 104, 231);

}

.white-tile {
    display: grid;
    place-content: center;
    background-color: #ebecd0;
}
